@import url(https://fonts.googleapis.com/css?family=Orbitron:400,500,700);
html, body, #root 
{
    font-family: 'Orbitron', 'Open Sans', serif;
    min-width: 100vw;
    min-height: 100vh;
    margin: 0 auto;
    text-align: center;
    background-color: rgb(255, 255, 255);
    overflow: hidden;
    box-shadow: inset 0 0 3.8em rgba(255,255,255,0.3), 0 0 3.8em rgba(255,255,255,0.3);
}
#titleMenu
{
    height: 150vh;
    width: 35vw;
    margin: 0 auto;
    margin-top: -25vh;
    margin-bottom: -25vh;
    background-color: rgb(31, 30, 30);
    box-shadow: inset 0 0 0 rgba(31,30,30,0.5), 0 0 1.5em rgba(31,30,30,0.7);
    border: black solid 2px;
    border-radius: 50%;
    transition: 2.0s;
}
#titleMenu:hover 
{
    box-shadow: inset 0 0 2.0em rgba(31,30,30,0.5), 0 0 2.5em rgba(31,30,30,0.7);
    width: 40vw;
    transition: 2.0s;
}
img 
{
    margin-top: 50vh;
    margin-bottom: 5vh;
    height: 35vh;
    width: 25vw;
}
#actionPanel 
{
    margin-top: 0vh;
}
#actionPanel > button 
{
    display: block;
    margin: 3vh auto;
    height: 6vh;
    width: 13vw;
    font-size: 1.6vh;
    background-color:  rgba(63, 63, 63, 0.1);
    box-shadow: inset 0 0 1em rgba(255, 255, 255, 0.5), 0 0 1em rgba(255, 255, 255, 0.5);
    border: rgb(255, 255, 255) solid 2px;
    cursor: pointer;
    transition: 0.3s;
}
#actionPanel > button:hover 
{
    color: black;
    background-color: rgb(255, 255, 255);
    box-shadow: inset 0 0 1em rgba(255, 255, 255, 0.5), 0 0 1em rgba(255, 255, 255, 0.5);
    transition: 0.3s;
}
@-webkit-keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
/*
 * CHARACTER CREATION
 */
#characterCreation 
{
    height: 100vh;
    width: 100vw;
    font-family: 'Orbitron', 'Open Sans', serif;
    font-size: 2vh;
    font-weight: normal;
    letter-spacing: 0.30em;
    background-color: rgb(31, 30, 30);
    box-shadow: inset 0 0 3.8em rgba(255,255,255,0.3), 0 0 3.8em rgba(255,255,255,0.3);
}
#playerName, #playerClass, #playerProficiency
{
    height: 66vh;
    width: 20vw;
    margin: 15vh 3vw 3vw 3vw;
    display: inline-block; 
    vertical-align: top;
    color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    transition: all 1s;
    opacity: 0;
    animation:fadeIn ease-in 1; -webkit-animation:fadeIn ease-in 1; -moz-animation:fadeIn ease-in 1;
    animation-fill-mode:forwards; -webkit-animation-fill-mode:forwards; -moz-animation-fill-mode:forwards;
    animation-duration:1s; -webkit-animation-duration:1s; -moz-animation-duration:1s;
}
#playerName:hover, #playerClass:hover, #playerProficiency:hover 
{
    color: rgb(31, 30, 30);
    background-color: rgb(255, 255, 255);
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    border: black solid 2px;
    transition: 1.0s;
}
#characterCreation > * > label 
{
    display: block;
    font-size: 3.5vh;
}
#characterCreation > * > input, #characterCreation > * > * > button
{
    color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    transition: 0.5s;
}
#characterCreation > * > input
{
    height: 4vh;
    width: 10vw;
    font-size: 1.8vh;
    margin: 0 auto;
    text-align: center;
    font-family: 'Orbitron', 'Open Sans', serif;
}
#characterCreation > * > * > button 
{
    cursor: pointer;
    color: rgb(255, 255, 255);
}
#characterCreation > * > input:hover, #characterCreation > * > * > button:hover 
{
    box-shadow: inset 0 0 0 rgba(31,30,30,0.5), 0 0 1.5em rgba(31,30,30,0.7);
    border: black solid 2px;
    transition: 0.5s;
}
/*
 * PLAYER NAME
 */
#playerName 
{
    animation-delay: 0.7s; -webkit-animation-delay: 0.7s; -moz-animation-delay: 0.7s;
}
#playerName > label 
{
    margin: 6vh 0 18vh 0;
}
#nameInput
{
    background-color: white;

}
/*
 * PLAYER CLASS
 */
#playerClass 
{
    animation-delay: 1.2s; -webkit-animation-delay: 1.2s; -moz-animation-delay:1.2s;
}
#playerClass > label 
{
    margin: 6vh 0 1vh 0;
}
#playerClass > * > button 
{
    height: 8.5vh;
    width: 13vw;
    margin: 3vh 1vw .4vh 1vw;
    font-size: 1.8vh;
}
/*
 * PLAYER PROFICIENCY
 */
#playerProficiency
{
    animation-delay: 1.6s; -webkit-animation-delay: 1.6s; -moz-animation-delay: 1.6s;
}
#playerProficiency > label 
{
    margin: 6vh 0 1vh 0;
}
#playerProficiency > * > button
{
    height: 6vh;
    width: 13vw;
    margin: 3vh 1vw -1.4vh 1vw;
    font-size: 1.8vh;
}
/*
 * START GAME
 */
#startGame 
{
    margin: 0 auto;
    margin-top: -10px;
    padding: 0;
    clear: both;
    display: block;
    text-align: center;
}
#startGame > button
{
    margin: 0;
}
.notVisible 
{
    opacity: 0;
    color: rgba(0,0,0,0.8);
    background-color: black;
    box-shadow: none;
    transition: 0.0s;

}
.notVisible:hover 
{
    background-color: black;
    transition: 0.0s;
}
.visible 
{
    height: 7.5vh;
    width: 13vw;
    opacity: 0;
    font-size: 1.8vh;
    color: rgb(31, 30, 30);
    background-color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    transition: .2s;
    cursor: pointer;
    animation-delay: 1s; -webkit-animation-delay: 1s; -moz-animation-delay: 1s;
    animation:fadeIn ease-in 1; -webkit-animation:fadeIn ease-in 1; -moz-animation:fadeIn ease-in 1;
    animation-fill-mode:forwards; -webkit-animation-fill-mode:forwards; -moz-animation-fill-mode:forwards;
    animation-duration:1s; -webkit-animation-duration:1s; -moz-animation-duration:1s;
}
.visible:hover 
{
    height: 9vh;
    width: 14vw;
    transition: .2s;
}
#navigation 
{
    height: 25vh;
    width: 100vw;
}

#navButton
{
    width: 15.2vw;
    height: 2vh;
    margin: 1vw;
    margin-top: 20vh;
    font-size: 1.8vh;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    overflow: hidden;
    letter-spacing: 0.30em;
    transition: 0.3s;
}
#navButton:hover
{
    width: 15vw;
    height: 5vh;
    margin: -10vh 2vw -10vh 2vw;
    color: rgb(31, 30, 30);
    box-shadow: inset 0 0 0 rgba(31,30,30,0.5), 0 0 1.5em rgba(31,30,30,0.7);
    transition: 0.3s;
}
#characterStatus 
{
    height: 65vh;
    width: 85vw;
    margin: 0 auto;
    letter-spacing: 0.07em;
    font-size: 2vh;

}
#statusPanel, #attributePanel, #skillPanel
{
    margin: 0 auto;
    height: 60vh;
    width: 15vw;
    margin: 1vw;
    color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    display: inline-block;
    overflow: hidden;
    transition: all 1s;
    opacity:0;
    animation:fadeIn ease-in 1; -webkit-animation:fadeIn ease-in 1; -moz-animation:fadeIn ease-in 1;
    animation-fill-mode:forwards; -webkit-animation-fill-mode:forwards; -moz-animation-fill-mode:forwards;
    animation-duration:1s; -webkit-animation-duration:1s; -moz-animation-duration:1s;
}
#statusPanel:hover, #attributePanel:hover, #skillPanel:hover 
{
    color: rgb(31, 30, 30);
    background-color: rgb(255, 255, 255);
    transition: 1.0s;
}
#statusPanel
{
    animation-delay: 0.5s; -webkit-animation-delay: 0.5s; -moz-animation-delay: 0.5s;
}
#attributePanel
{
    animation-delay: 1s; -webkit-animation-delay: 1s; -moz-animation-delay: 1s;
}
#skillPanel
{
    animation-delay: 1.4s; -webkit-animation-delay: 1.4s; -moz-animation-delay: 1.4s;
}
#characterStatus > * > table
{
    margin: 0 auto;
}
#characterStatus > * > * > * > td
{
    text-align: left;
    padding: 1vh;
}
#gameSpace
{
    height: 100vh;
    width: 100vw;
    background-color: rgb(31, 30, 30);
    box-shadow: inset 0 0 3em rgba(255,255,255,0.3), 0 0 3em rgba(255,255,255,0.3);
    overflow: hidden;
}
#activityFeed 
{
    height: 40vh;
    width: 45vw;
    margin: 0 auto;
    padding: 2vw;
    color: rgb(255, 255, 255);
    text-align: center;
    font-style: italic;
    font-family: Monospace, 'Open Sans', serif;
    font-weight: 600;
    overflow: hidden;
}
#message
{
    margin-top: 15vh;
    text-align: left;
    font-size: 3vh;
    display: inline-block;
}
#character
{
    opacity: 0;
    animation: fadeIn ease-in 1; -webkit-animation:fadeIn ease-in 1; -moz-animation: fadeIn ease-in 1;
    animation-fill-mode: forwards; -webkit-animation-fill-mode: forwards; -moz-animation-fill-mode: forwards;
    animation-duration: 0.8s; -webkit-animation-duration: 0.8s; -moz-animation-duration: 0.8s;
}
#actionBar 
{
    margin: 0 auto;
    margin-top: 2vh;
    height: 25vh;
    width: 56vw;
    text-align: center;
}
#actions 
{
    width: 100%;
    height: 100%;
    text-align: center;
    margin: 0 auto;
    padding-left: 13vw;
}
#actions > * > button 
{
    height: 6vh;
    width: 3vw;
    margin: 1vh 6vw 1vh 0vw;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
    border: rgb(31, 30, 30) solid 2px;
    overflow: hidden;
    float: left;
    transition: 0.5s;
}
#actions > * > button:hover
{
    color: black;
    box-shadow: inset 0 0 1.8em rgba(255,255,255,0.3), 0 0 1.8em rgba(255,255,255,0.3);
    transition: 0.5s;
}
@-webkit-keyframes moving {
    from { -webkit-transform: translateY(2vh); transform: translateY(2vh); }
    to { -webkit-transform: translateY(0vh); transform: translateY(0vh); }
}
@keyframes moving {
    from { -webkit-transform: translateY(2vh); transform: translateY(2vh); }
    to { -webkit-transform: translateY(0vh); transform: translateY(0vh); }
}
#actionMessage
{
    height: 4vh;
    width: 50vw;
    margin: 0 auto;
    color: rgb(255, 255, 255);
    margin-top: 3vh;
    margin-bottom: 3vh;
    font-size: 2.5vh;
    clear: both;
}
.move
{
    -webkit-animation: moving 0.8s 1;
    animation: moving 0.8s 1;
}
button {
    font-family: 'Orbitron', 'Open Sans', serif;
    font-size: 14px;
    font-weight: 500;
    color: #FFF;
    height: 45px;
    width: 155px;
    cursor: pointer;
    display: inline-block;
}
