#characterStatus 
{
    height: 65vh;
    width: 85vw;
    margin: 0 auto;
    letter-spacing: 0.07em;
    font-size: 2vh;

}
#statusPanel, #attributePanel, #skillPanel
{
    margin: 0 auto;
    height: 60vh;
    width: 15vw;
    margin: 1vw;
    color: rgb(255, 255, 255);
    border: rgb(255, 255, 255) solid 2px;
    box-shadow: inset 0 0 0.8em rgba(255,255,255,0.3), 0 0 0.8em rgba(255,255,255,0.3);
    display: inline-block;
    overflow: hidden;
    transition: all 1s;
    opacity:0;
    animation:fadeIn ease-in 1; -webkit-animation:fadeIn ease-in 1; -moz-animation:fadeIn ease-in 1;
    animation-fill-mode:forwards; -webkit-animation-fill-mode:forwards; -moz-animation-fill-mode:forwards;
    animation-duration:1s; -webkit-animation-duration:1s; -moz-animation-duration:1s;
}
#statusPanel:hover, #attributePanel:hover, #skillPanel:hover 
{
    color: rgb(31, 30, 30);
    background-color: rgb(255, 255, 255);
    transition: 1.0s;
}
#statusPanel
{
    animation-delay: 0.5s; -webkit-animation-delay: 0.5s; -moz-animation-delay: 0.5s;
}
#attributePanel
{
    animation-delay: 1s; -webkit-animation-delay: 1s; -moz-animation-delay: 1s;
}
#skillPanel
{
    animation-delay: 1.4s; -webkit-animation-delay: 1.4s; -moz-animation-delay: 1.4s;
}
#characterStatus > * > table
{
    margin: 0 auto;
}
#characterStatus > * > * > * > td
{
    text-align: left;
    padding: 1vh;
}